<template>
  <li @click="$router.push('/playlist?id=' + item.id)" class="card-item">
    <div class="thumb">
      <img :src="`${item.picUrl}?imageView=1&type=webp&thumbnail=246x0`" />
      <span
        ><img
          style="width: 11px"
          src="data:image/svg+xml;base64,"
          alt=""
        />
        {{ item.playCount | parseCount }}</span
      >
    </div>
    <p>{{ item.name }}</p>
  </li>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
      required: true,
    },
  },

  filters: {
    parseCount: function (value) {
      if (value > 100000000) {
        return (value / 100000000).toFixed(1) + "亿";
      } else if (value > 10000) {
        return (value / 10000).toFixed(1) + "万";
      } else {
        return value;
      }
    },
  },
};
</script>

<style lang="less" scoped>
.card-item {
  flex-basis: 30%;
  flex-grow: 1;
  &:nth-child(3n + 2) {
    // background: red;
    margin: 0 3px;
  }
  .thumb {
    position: relative;
    top: 0;
    left: 0;
    // background: red;
    img {
      vertical-align: middle;
    }
    span {
      position: absolute;
      top: 2px;
      right: 5px;
      color: #fff;
      font-size: 12px;
    }
  }
  p {
    //   显示2行  溢出隐藏
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    font-size: 13px;
    margin: 4px 10px 14px 6px;
  }
}
</style>